<template>
  <div>
    <span v-bind:style="{ backgroundColor: currentColor }" class="bar top-bar"></span>
    <div class="container">
      <div class="hero-container">
        <h1
          v-bind:style="{ backgroundImage: `url(${currentBg})` }"
        >Hi! <br /> I'm Sergio!</h1>
      </div>
      <div v-bind:style="{ borderLeft: `2px solid ${currentColor}` }" class="side-container" >
        <p>I'm a web developer located in BA, currently working for Auth0.</p>
        <p> Dynamic and detail-oriented programmer with a focus for delivering elegant solutions for UI interfaces, effectively and efficiently.
I possess an extensive understanding of responsive web design, mobile first practices, modern layout building, and cross browser compatibility
</p><p>I have a track record of working in several fields. I've done sites for brands like Nike and Samsung,
mobile videogames, medical products, and currently in a SaaS company.</p>
        <div
          :style="style" class="color-palette"
          v-on:click="updateColor"
        >
          <span class="color-circle color-1"></span>
          <span class="color-circle color-2"></span>
          <span class="color-circle color-3"></span>
        </div>
        <SocialBar />
      </div>
    </div>
    <span v-bind:style="{ backgroundColor: currentColor }" class="bar bottom-bar"></span>
  </div>
</template>

<script>
import SideBar from './SideBar'
import SocialBar from './SocialBar'

export default {
  name: 'Homepage',
  components: {
    SideBar,
    SocialBar,
  },
  data() {
    return {
      startingColor: 0,
      currentColor: '#11cbdc',
      currentRotation: 60,
      currentBg: 'http://colrd.com/misc/library/image/4edd15555595e725516f3af7a251a1ad/4edd15555595e725516f3af7a251a1ad.detail.jpeg',
    };
  },
  computed: {
    style () {
      return { transform: 'rotate(' + this.currentRotation + 'deg)'}
    }
  },
  methods: {
    updateColor: function () {
      const colors = ['#11cbdc', '#FDB52B', '#E0292D'];
      const bgImages = [
        'http://colrd.com/misc/library/image/4edd15555595e725516f3af7a251a1ad/4edd15555595e725516f3af7a251a1ad.detail.jpeg',
        'https://www.floraqueen.com/blog/wp-content/uploads/2017/10/Untitled-design-1-6.jpg',
        'https://www.justmytype.ca/wp-content/uploads/2012/10/red-trees-Pacheco.jpg'
      ]
      if (this.startingColor < 2) {
        this.startingColor = this.startingColor + 1;
        this.currentRotation = this.currentRotation - 120;
      }
      else {
        this.startingColor = 0;
        this.currentRotation = this.currentRotation - 120;
      }
      this.currentBg = bgImages[this.startingColor]
      this.currentColor = colors[this.startingColor];
    }
  }
};


</script>

<style scoped>
.container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
  text-align: left;
}

.hero-container {
}

.side-container {
}

@media screen and (min-width: 768px) {
  .container {
    flex-direction: row;
    align-items: center;
  }

  .hero-container {
    flex: 1;
  }

  .side-container {
    flex: 1;
    padding: 0px 4rem;
  }
}


h1 {
  padding: 8px;
  margin-left: 2rem;
  font-size: 10rem;
  font-weight: 900;
  line-height: 1;
  color: white;
  transition: all 1s ease;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

p {
  font-size: 2rem;
  margin: 0;
  color: rgba(255, 255, 255, .9);
}

span.bar {
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  height: 4.5rem;
  background-color: black;
  transition: all 1s ease;
}

.color-palette {
  position: relative;
  display: flex;
  width: 80px;
  height: 75px;
  margin: 40px 0;
  transform-origin: center center;
  transition: all .35s ease-in;
  animation: highlight .75s ease-out .5s forwards;
  opacity: 0.2;
}

.color-palette:hover,
.color-palette:focus {
  cursor: pointer;
}

@keyframes highlight {
  0% {
    opacity: 0.2;
  }

  100% {
    opacity: 1;
  }
}
span.color-circle {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
}

.color-1 {
  position: absolute;
  top:0;
  left: 0;
  background-color: #11cbdc;
}

.color-2 {
  position: absolute;
  top:0;
  left: 30px;
  background-color: #FDB52B;
}

.color-3 {
  position: absolute;
  top: 25px;
  left: 15px;
  background-color: #E0292D;
}

button {
  padding: 10px 20px;
  margin-top: 2.5rem;
  background: transparent;
  border-radius: 3px;
  font-size: 12px;
  text-transform: uppercase;
  color: white;
  cursor: pointer;
}

button:focus,
button:hover,
button:active {
  outline: none;
}


span.top-bar {
  top: 0;
}

span.bottom-bar {
  bottom: 0;
}

</style>



